body {
    font-family: "Open Sans", sans-serif;
    font-size: 16px;
    line-height: 20px;
  }
  
  header {
    background-color: #2e64a1;
    padding: 16px 0; /* equivalente a 1rem 0 si el tamaño de fuente base es 16px */
  }
  
  header h1 {
    color: white;
    font-size: 40px;
    font-weight: 100;
    font-style: italic;
    display: inline-block;
    position: relative;
    bottom: 35px;
    margin-left: 10px;
  }
  
  nav {
    background-color: #6da9ed;
    margin-bottom: 20px;
  }
  
  nav ul {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
  }
  
  nav ul li {
    flex-grow: 1;
  }
  
  nav ul li a {
    color: white;
    text-decoration: none;
    padding: 3px 6px;
    display: block;
    text-align: center;
  }
  
  nav ul li a:hover {
    background-color: rgba(0, 0, 0, 0.3);
  }
  
  footer {
    background-color: #ccc; /* Color de fondo gris claro */
    clear: both; /* Limpia los flotados, si los hubiera, para evitar solapamientos */
    color: #333; /* Color de texto gris oscuro */
    text-align: center; /* Alinea el texto al centro */
    padding: 5px 0; /* Relleno superior e inferior de 5px, sin relleno horizontal */
    margin-top: 20px; /* Margen superior de 20px */
  }
  
  nav ul li a.activo {
    background-color: #4c76a5;
  }
  
  .holder {
    max-width: 960px;
    margin: 0 auto;
  }
  
  h2 {
    color: #2e64a1;
    font-weight: 400;
  }
  
  .bienvenidos {
    width: 75%;
    padding-right: 10px;
    box-sizing: border-box;
  }
  
  .testimonios {
    width: 25%;
  }
  
  .columnas {
    display: flex;
    justify-content: space-between;
  }
  
  .bienvenidos p {
    font-size: 20px;
  }
  
  .testimonio {
    background-color: #ccc;
    padding: 16px;
  }
  
  .testimonio .cita {
    font-weight: bold;
    font-style: italic;
    font-size: 22px;
    display: block;
  }
  
  .testimonio .autor {
    font-size: 10px;
    text-align: center;
    display: block;
    margin: 12px 0;
  }
  